// Countdown Timer

function initializeCountdown(endDate, elementId, prefixText = "") {
  const element = document.getElementById(elementId);
  const headerTextElement = document.getElementById("countdown-header-text");
  const introTextElement = document.getElementById("intro__header");
  if (!element) {
    console.warn(`Element with ID ${elementId} not found`);
    return;
  }

  const updateCountdown = () => {
    const now = new Date().getTime();
    const distance = endDate - now;

    if (distance < 0) {
      clearInterval(countdownInterval);
      element.innerHTML = `Welcome to CryptoGetExpo`;
      element.style.fontFamily = "var(--font-press-start)";
      headerTextElement.style.display = "none";
      introTextElement.style.display = "none";
      return;
    }

    const days = Math.floor(distance / (1000 * 60 * 60 * 24));
    const hours = Math.floor(
      (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
    );
    const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((distance % (1000 * 60)) / 1000);

    element.innerHTML = `${prefixText}${days}d ${hours}h ${minutes}m ${seconds}s`;
  };

  updateCountdown();
  const countdownInterval = setInterval(updateCountdown, 1000);
}

const eventDate = new Date("July 15, 2024 17:30:00").getTime();
initializeCountdown(eventDate, "intro-countdown");
initializeCountdown(eventDate, "header-countdown", "Starts in: ");
initializeCountdown(eventDate, "main-countdown");
