window.addEventListener("click", function (event) {
  if (event.target === lightbox) {
    lightbox.classList.remove("show");
    lightbox.querySelector(".lightbox-content").classList.remove("show");
  }
});

// Function to handle intersection observer

const observeElements = (elements, observerOptions) => {
  const observer = new IntersectionObserver((entries, observer) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        entry.target.classList.add("visible");
        observer.unobserve(entry.target);
      }
    });
  }, observerOptions);

  elements.forEach((element) => {
    observer.observe(element);
  });
};

// Initialize intersection observer for sections

const sections = document.querySelectorAll(".section-animate");
observeElements(sections, { threshold: 0.1 });

// Initialize intersection observer for program list items

const programListItems = document.querySelectorAll(".program-list__item");
observeElements(programListItems, { threshold: 0.1 });

// Initialize intersection observer for speaker and sponsor cards

const speakerCards = document.querySelectorAll(".speaker");
const sponsorCards = document.querySelectorAll(".sponsor");
observeElements(speakerCards, { threshold: 0.1 });
observeElements(sponsorCards, { threshold: 0.1 });

